// 主要设置app开发模式
// 设置环境变量

import devConf from './.env.dev'
import testConf from './.env.test'
import prodConf from './.env.prod'
import { App } from 'vue'

const ENV = {
    DEV: 'dev', // 开发模式
    TEST: 'test', // 测试环境
    PROD: 'prod' // 生成环境
}

const envConf = {
    dev: devConf,
    test: testConf,
    prod: prodConf
}

// 当前环境变量
let ENV_KEY = 'dev'

// app手动设置环境变量
// #ifdef APP-PLUS
ENV_KEY = ENV.DEV
// #endif

// h5 微信小程序开发环境
// #ifdef H5-DEV || WX-DEV
ENV_KEY = ENV.DEV
// #endif

// h5 微信小程序测试环境
// #ifdef H5-TEST || WX-TEST
ENV_KEY = ENV.TEST
// #endif

// h5 微信小程序生产环境
// #ifdef H5-PROD || WX-PROD
ENV_KEY = ENV.PROD
// #endif

// 配置信息
const config = envConf[ENV_KEY]

// 设置版本号
config.version = 'v20220113'

const uniEnv = {}

for (const key in config) uniEnv[key] = config[key]

uni.$env = uniEnv

const install = (app: App) => {
    app.config.globalProperties.$env = uniEnv

    if (['dev', 'test'].indexOf(ENV_KEY) !== -1) {
        uni.$m.toast(`[${ENV_KEY}]: ${uniEnv.version}`)
    }
}

export default {
    install
}
