import { App } from 'vue'
import setup from './setup'

interface Conf {
    msg: any,
    tip?: string,
    env?: string[]
}

function log(config: Conf | string) {
    const { msg, tip = '', env = ['dev'] } = config

    if (env.indexOf(uni.$env.env) === -1) return

    if (uni.$m.test.string(config)) {
        console.log(`[${uni.$env.env}] start`)
        console.log(config)
        console.log(`[${uni.$env.env}] end`)
        console.log('')
        return
    }

    console.log(`[${uni.$env.env}] ${tip ? `${tip} ` : ''}start`)
    console.log(msg)
    console.log(`[${uni.$env.env}] ${tip ? `${tip} ` : ''}end`)
    console.log('')
}

function debug(str: string, env = ['dev']) {

    if (env.indexOf(uni.$env.env) === -1) return

    uni.$m.toast(`[${uni.$env.env}]: ${str}`)
}

const common = {
    $setup: setup,
    $log: log,
    $debug: debug
}

// #ifdef H5
window.NProgress && (common.$NP = window.NProgress)
// #endif

for (const c in common) {
    uni[c] = common[c]
}

export default (app: App) => {
    for (const c in common) {
        app.config.globalProperties[c] = common[c]
    }
}
