import { App } from 'vue'

export default (app: App) => {
    uni.$m.http.interceptors.request.use(
        config => {
            uni.showLoading({
                title: '加载中...',
                mask: true
            })

            uni.$log({
                msg: config,
                tip: '请求拦截'
            })

            return config
        },
        error => {
            uni.hideLoading()
            uni.$log({
                msg: error,
                tip: '请求拦截错误'
            })

            return Promise.reject(error)
        }
    )
}
