import { defineStore } from 'pinia'
// import { i18 } from '@/lang'

export const useCommonStore = defineStore('$common', {
    state() {
        return {
            locale: 'zh-CN'
        }
    },
    getters: {},
    actions: {
        setLocale(val) {
            const names = uni.$lang.names.map(item => item.name)
            if (names.indexOf(val) === -1 || val === this.locale) return
            this.locale = val
            i18.global.locale = val
        }
    },
    persist: {
        enabled: true
    }
})
