/**
 * @Author: ManKeung
 * @description: 路由跳转拦截器
 */

// 需要拦截的路由跳转
const list = ['navigateTo', 'redirectTo', 'reLaunch', 'switchTab']

export default () => {
    list.forEach(item => {
        uni.addInterceptor(item, {
            invoke(e) {

                // #ifdef H5
                uni.$NP.start()
                // #endif

                if (!uni.$task || !uni.$task.length) return e

                // 取消请求
                uni.$task.forEach((task, index) => {
                    task.abort()

                    delete uni.$task[index]
                })

                return e
            },
            success() {},
            fail(_err) {
                uni.navigateTo({
                    url: '/pages/error/404'
                })
            },
            complete() {
                // #ifdef H5
                uni.$NP.done()
                // #endif
            }
        })
    })
}
