import { App } from 'vue'
import { createI18n } from 'vue-i18n'
import { useCommonStore } from '@/stores/common'
import zhHans from './zh-Hans.json'
import zhHant from './zh-Hant.json'
import en from './en.json'

const messages = {
    en,
    'zh-Hans': zhHans,
    'zh-Hant': zhHant
}

export const i18 = createI18n({
    // legacy: false,
    globalInjection: true,
    locale: 'zh-Hans',
    messages
})

// 语言集合
const names = []
for (const m in messages ) {
    names.push({
        name: m,
        title: messages[m][`locale.${m}`] ?? '未知'
    })
}

messages.names = names

// $m挂载到uni对象上
uni.$lang = messages

const install = (app: App) => {
    const common = useCommonStore()
    i18.global.locale = common.$state.locale || uni.getLocale() || 'zh-Hans'
    app.use(i18)
    app.config.globalProperties.$lang = messages
}

export default {
    install
}
