import { PiniaPluginContext } from 'pinia'

export interface PersistStrategy {
    key?: string
    storage?: Storage
    paths?: string[]
}

export interface PersistOptions {
    enabled: true
    strategies?: PersistStrategy[]
}

type Store = PiniaPluginContext['store']
type PartialState = Partial<Store['$state']>

declare module 'pinia' {
    export interface DefineStoreOptionsBase<S, Store> {
        persist?: PersistOptions
    }
}

function setStorage(strategy: PersistStrategy, store: Store, val: any) {
    const key = strategy.key || store.$id

    // #ifndef H5
    uni.setStorage({
        key,
        data: JSON.stringify(val)
    })
    // #endif

    // #ifdef H5
    const storage = strategy.storage || localStorage

    storage.setItem(key, JSON.stringify(val))
    // #endif
}

function getStorage(strategy: PersistStrategy, store: Store) {
    const key = strategy.key || store.$id

    // #ifndef H5
    console.log(uni.getStorageSync(key))
    return JSON.parse(uni.getStorageSync(key) || JSON.stringify({}))
    // #endif

    // #ifdef H5
    const storage = strategy.storage || localStorage

    return JSON.parse(storage.getItem(key) || JSON.stringify({}))
    // #endif
}

export const updateStorage = (strategy: PersistStrategy, store: Store) => {

    if (strategy.paths) {
        const partialState = strategy.paths.reduce((finalObj, key) => {
            finalObj[key] = store.$state[key]
            return finalObj
        }, {} as PartialState)

        setStorage(strategy, store, partialState)
    } else {
        setStorage(strategy, store, store.$state)
    }
}

export default ({ options, store }: PiniaPluginContext): void => {
    if (options.persist?.enabled) {
        const defaultStrat: PersistStrategy[] = [{
            key: store.$id
        }]

        const strategies = options.persist?.strategies?.length ? options.persist?.strategies : defaultStrat

        strategies.forEach((strategy) => {
            const storeKey = strategy.key || store.$id
            const storageResult = getStorage(strategy, store)

            if (storageResult) {
                store.$patch(storageResult)
                updateStorage(strategy, store)
            }
        })

        store.$subscribe(() => {
            strategies.forEach((strategy) => {
                updateStorage(strategy, store)
            })
        })
    }
}
