import { App } from 'vue'

export default (app: App) => {
    uni.$m.http.interceptors.response.use(
        response => {
            uni.hideLoading()

            uni.$log({
                msg: response,
                tip: '响应拦截'
            })
        },
        error => {
            uni.hideLoading()

            uni.$log({
                msg: error,
                tip: '请求拦截错误'
            })

            return Promise.reject(error)
        }
    )
}
